import { createStore } from 'vuex'
import axios from 'axios'

const API_URL = 'https://tably.sbs/api'

// –ü–æ–ª—É—á–∞–µ–º –∫–æ—Ä–∑–∏–Ω—É –∏ —Å–µ—Å—Å–∏—é –∏–∑ localStorage –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏
const savedCart = localStorage.getItem('cart')
const initialCart = savedCart ? JSON.parse(savedCart) : { items: [], total: 0 }
const sessionId = localStorage.getItem('sessionId')

export default createStore({
  state: {
    sessionId: sessionId || null,
    tableId: localStorage.getItem('tableId') || null,
    restaurant: null,
    menuCategories: [],
    cart: initialCart,
    loading: false,
    error: null
  },
  getters: {
    cartItemCount(state) {
      return state.cart.items.reduce((total, item) => total + (item.quantity || 0), 0)
    },
    cartTotal(state) {
      return state.cart.items.reduce((total, item) => total + ((item.price || 0) * (item.quantity || 0)), 0)
    },
    isSessionActive(state) {
      return !!state.sessionId
    }
  },
  mutations: {
    setSessionId(state, sessionId) {
      state.sessionId = sessionId
      if (sessionId) {
        localStorage.setItem('sessionId', sessionId)
        localStorage.setItem('createdAt', Date.now())
      } else {
        localStorage.removeItem('sessionId')
        localStorage.removeItem('createdAt')
      }
    },
    setTableId(state, tableId) {
      state.tableId = tableId
      if (tableId) {
        localStorage.setItem('tableId', tableId)
      } else {
        localStorage.removeItem('tableId')
      }
    },
    setTable(state, table) {
      state.table = table
    },
    setRestaurant(state, restaurant) {
      state.restaurant = restaurant
    },
    setMenuCategories(state, categories) {
      state.menuCategories = categories
    },
    addToCart(state, { menuItem, quantity = 1 }) {
      const existingItem = state.cart.items.find(item => item.id === menuItem.id)

      if (existingItem) {
        existingItem.quantity += quantity
      } else {
        state.cart.items.push({
          id: menuItem.id,
          name: menuItem.name,
          price: menuItem.price,
          image: menuItem.image,
          quantity: quantity
        })
      }

      // –ü–µ—Ä–µ—Å—á–∏—Ç—ã–≤–∞–µ–º –æ–±—â—É—é —Å—É–º–º—É
      state.cart.total = state.cart.items.reduce(
        (total, item) => total + (item.price * item.quantity),
        0
      )

      // –°–æ—Ö—Ä–∞–Ω—è–µ–º –∫–æ—Ä–∑–∏–Ω—É –≤ localStorage
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    updateCartItem(state, { itemId, quantity }) {
      const item = state.cart.items.find(item => item.id === itemId)

      if (item) {
        item.quantity = quantity

        // –ü–µ—Ä–µ—Å—á–∏—Ç—ã–≤–∞–µ–º –æ–±—â—É—é —Å—É–º–º—É
        state.cart.total = state.cart.items.reduce(
          (total, item) => total + (item.price * item.quantity),
          0
        )

        // –°–æ—Ö—Ä–∞–Ω—è–µ–º –∫–æ—Ä–∑–∏–Ω—É –≤ localStorage
        localStorage.setItem('cart', JSON.stringify(state.cart))
      }
    },
    removeFromCart(state, itemId) {
      state.cart.items = state.cart.items.filter(item => item.id !== itemId)

      // –ü–µ—Ä–µ—Å—á–∏—Ç—ã–≤–∞–µ–º –æ–±—â—É—é —Å—É–º–º—É
      state.cart.total = state.cart.items.reduce(
        (total, item) => total + (item.price * item.quantity),
        0
      )

      // –°–æ—Ö—Ä–∞–Ω—è–µ–º –∫–æ—Ä–∑–∏–Ω—É –≤ localStorage
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    clearCart(state) {
      state.cart = { items: [], total: 0 }

      // –û—á–∏—â–∞–µ–º –∫–æ—Ä–∑–∏–Ω—É –≤ localStorage
      localStorage.removeItem('cart')
    },
    setLoading(state, loading) {
      state.loading = loading
    },
    setError(state, error) {
      state.error = error
    }
  },
  actions: {
    // –°–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–π —Å–µ—Å—Å–∏–∏
    async createSession({ commit }, tableId) {
      try {
        commit('setLoading', true)

        const response = await axios.post(`${API_URL}/session/create`, {
          table_id: tableId
        })

        if (response.data && response.data.session_id) {
          commit('setSessionId', response.data.session_id)
          commit('setTableId', tableId)
          commit('setError', null)
          return { success: true, sessionId: response.data.session_id }
        } else {
          commit('setError', '–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å —Å–µ—Å—Å–∏—é')
          return { success: false, error: '–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å —Å–µ—Å—Å–∏—é' }
        }
      } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ —Å–µ—Å—Å–∏–∏:', error)

        let errorMessage = '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ —Å–µ—Å—Å–∏–∏'

        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞
        if (error.response && error.response.data && error.response.data.error) {
          errorMessage = error.response.data.error
        }

        commit('setError', errorMessage)
        return { success: false, error: errorMessage }
      } finally {
        commit('setLoading', false)
      }
    },

    // –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Å—Ç–æ–ª–∞ –∏ –º–µ–Ω—é
    async loadTableData({ commit, state, dispatch }, tableId) {
      try {
        commit('setLoading', true)

        // –°–æ—Ö—Ä–∞–Ω—è–µ–º ID —Å—Ç–æ–ª–∞
        commit('setTableId', tableId)

        // –ï—Å–ª–∏ –Ω–µ—Ç –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ—Å—Å–∏–∏, –ø—ã—Ç–∞–µ–º—Å—è —Å–æ–∑–¥–∞—Ç—å –Ω–æ–≤—É—é
        if (!state.sessionId) {

          const sessionResult = await dispatch('createSession', tableId)
          if (!sessionResult.success) {
            return { success: false, error: sessionResult.error }
          }
        }
        if (localStorage.getItem('createdAt') && Date.now() - localStorage.getItem('createdAt') > 10 * 60 * 1000) {
          localStorage.removeItem('createdAt')
          localStorage.removeItem('sessionId')
          const sessionResult = await dispatch('createSession', tableId)
          if (!sessionResult.success) {
            return { success: false, error: sessionResult.error }
          }
        }
        // –ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ —Å—Ç–æ–ª–∞ –∏ –º–µ–Ω—é
        const response = await axios.get(`${API_URL}/tables/${tableId}`)

        commit('setRestaurant', response.data.restaurant)
        commit('setMenuCategories', response.data.menu_categories)
        commit('setTable', response.data.table)
        commit('setError', null)

        return { success: true }
      } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö —Å—Ç–æ–ª–∞:', error)

        let errorMessage = '–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö —Å—Ç–æ–ª–∞'

        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞
        if (error.response && error.response.data && error.response.data.error) {
          errorMessage = error.response.data.error
        }

        commit('setError', errorMessage)
        return { success: false, error: errorMessage }
      } finally {
        commit('setLoading', false)
      }
    },

    // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –≤ –∫–æ—Ä–∑–∏–Ω—É
    addToCart({ commit, state }, { menuItemId, quantity = 1 }) {
      // –ù–∞—Ö–æ–¥–∏–º –±–ª—é–¥–æ –ø–æ ID
      let menuItem = null

      for (const category of state.menuCategories) {
        const foundItem = category.menu_items.find(item => item.id === menuItemId)
        if (foundItem) {
          menuItem = foundItem
          break
        }
      }

      if (menuItem) {
        commit('addToCart', { menuItem, quantity })
      }
    },

    // –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –≤ –∫–æ—Ä–∑–∏–Ω–µ
    updateCartItem({ commit }, { itemId, quantity }) {
      commit('updateCartItem', { itemId, quantity })
    },

    // –£–¥–∞–ª–µ–Ω–∏–µ –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
    removeFromCart({ commit }, itemId) {
      commit('removeFromCart', itemId)
    },

    // –û—á–∏—Å—Ç–∫–∞ –∫–æ—Ä–∑–∏–Ω—ã
    clearCart({ commit }) {
      commit('clearCart')
    },

    // –û—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ –∑–∞–∫–∞–∑–∞
    async checkout({ state, commit }, { paymentMethod = 'card' } = {}) {
      try {
        commit('setLoading', true)
        console.log('–ù–∞—á–∞–ª–æ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏—è –∑–∞–∫–∞–∑–∞, —Å–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã:', paymentMethod);
        console.log('–°–æ—Å—Ç–æ—è–Ω–∏–µ –∫–æ—Ä–∑–∏–Ω—ã:', state.cart);

        if (!state.sessionId) {
          console.error('–ù–µ—Ç –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ—Å—Å–∏–∏');
          commit('setError', '–ù–µ—Ç –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ—Å—Å–∏–∏')
          return { success: false, error: '–ù–µ—Ç –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ—Å—Å–∏–∏' }
        }

        // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–∫–∞–∑ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
        const orderData = {
          session_id: state.sessionId,
          payment_method: paymentMethod,
          items: state.cart.items.map(item => ({
            id: item.id,
            quantity: item.quantity,
            price: item.price
          })),
          total_price: state.cart.total
        }
        
        console.log('–î–∞–Ω–Ω—ã–µ –∑–∞–∫–∞–∑–∞ –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏:', orderData);

        // –î–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è, –µ—Å–ª–∏ API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –º–æ–∂–Ω–æ –≤–µ—Ä–Ω—É—Ç—å —É—Å–ø–µ—à–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç
        if (process.env.NODE_ENV === 'development' && !state.cart.items.length) {
          console.log('–¢–µ—Å—Ç–æ–≤—ã–π —Ä–µ–∂–∏–º: –≤–æ–∑–≤—Ä–∞—â–∞–µ–º —É—Å–ø–µ—à–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç');
          commit('clearCart')
          return {
            success: true,
            orderId: 'test-' + Date.now()
          }
        }

        const API_URL = axios.defaults.baseURL || '';
        const response = await axios.post(`${API_URL}/orders/create`, orderData, {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        })
        
        console.log('–û—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞:', response.data);

        if (response.data && response.data.order_id) {
          // –û—á–∏—â–∞–µ–º –∫–æ—Ä–∑–∏–Ω—É –ø–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏—è –∑–∞–∫–∞–∑–∞
          commit('clearCart')
          commit('setError', null)
          const result = {
            success: true,
            orderId: response.data.order_id
          };
          console.log('–£—Å–ø–µ—à–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç:', result);
          return result;
        } else {
          const errorMsg = '–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑';
          console.error(errorMsg);
          commit('setError', errorMsg)
          return { success: false, error: errorMsg }
        }
      } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏–∏ –∑–∞–∫–∞–∑–∞:', error)

        let errorMessage = '–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏–∏ –∑–∞–∫–∞–∑–∞'

        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞
        if (error.response && error.response.data && error.response.data.error) {
          errorMessage = error.response.data.error
        }

        commit('setError', errorMessage)
        return { success: false, error: errorMessage }
      } finally {
        commit('setLoading', false)
      }
    }
  }
}) 